import React from 'react';
import Swiper from 'react-id-swiper';
import classNames from 'classnames';
import Market from './MarketComponent';
import MarketSlides from './MarketSlides';
import BasketMobile from './icons/mobile/BasketMobile';

class MainContent extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showMobileAuth: this.props.showMobileAuth,
            categories: this.props.categories
        };
    }

    shouldComponentUpdate(nextProps, nextState) {
        if (nextProps.showMobileAuth !== this.state.showMobileAuth) {
            this.setState({ showMobileAuth: nextProps.showMobileAuth });

            return true;
        } else if (nextProps.categories && nextProps.categories !== this.state.categories) {
            this.setState({ categories: nextProps.categories });

            return true;
        }

        return false;
    }

    render() {
        return (
            <div className={classNames({ 'hide-element': this.state.showMobileAuth })}>
                <MarketSlides slides={this.props.slides} moreCssClass='desktop-only' />
                <MarketSlides slides={this.props.mobileSlides} moreCssClass='mobile-only' />
                <Market
                    cartPrice={this.props.cartPrice}
                    cartCount={this.props.cartCount}
                    categories={this.props.categories}
                    cart={this.props.cart}
                    addToCart={this.props.addToCart}
                    removeFromCart={this.props.removeFromCart}
                    recommendations={this.props.recommendations}
                />
            </div>
        );
    }
}

export default MainContent;
